.contact {
    padding-top: 12px;
    padding-bottom: 76px;
}
    @media screen and (min-width: 1444px) {
        .contact {
            padding-top: 24px;
            padding-bottom: 100px; 
        }
}
.contact__title {
    margin-bottom: 16px;
    text-transform: uppercase;
}
.contact__box-iframe {
    margin-bottom: 16px;
}
.contact__iframe {
    border-radius: 15px;
}

iframe {
    width: 280px;
    height: 171px;
}
    @media screen and (min-width: 768px) {
        iframe {
            width: 247px;
            height: 192px;
        }
    }
    @media screen and (min-width: 1444px) {
        iframe {
            width: 515px;
            height: 315px;
        }
    }

    .contact__description {
    &:not(:last-child) {
      margin-bottom: 16px;
    }
    @media screen and (min-width: 769px) {
        margin-bottom: 24px;
    }
  }

.contact__description {
    @include fontStyle(16px, 23px, var(--text-color), 0.06em, 300);
    font-family: var(--secondary-font);
    font-style: normal;
    @media screen and (min-width: $tablet) {
      font-size: 16px;
    }
    @media screen and (min-width: $desktop) {
      font-size: 18px;
    }
  }

  .contact__box-text {
      margin-bottom: 52px;
  }
  @media screen and (min-width: 768px) {
     .contact__box-text {
        margin-left: 20px;
     }
     .contact__form-name {
        text-align: center;
     }
     .contact__position-two {
         width: 420px;
         margin: 0 auto;
     }
     @media screen and (min-width: 1444px) {
        .contact__box-text {
            margin-bottom: 100px;
     }
     .contact__form-name {
         text-align: left;
     }
     .contact__position-two {
         margin: 0;
         margin-right: 110px;
     }
    }   
  }



.contact__form-name {
    margin-bottom: 8px;
}
.modal__form-input {
    font-size: 16px;
    
    display: block;
    width: 100%;
    border: none;
    border-bottom: 1px solid #487996;
}
@media screen and (min-width: 1444px) {
    .modal__form-input {
        width: 515px;
    }
}

.contact__row-box:nth-last-child(1n+3) {
    margin-bottom: 44px;
}

.modal__form-message {
    width: 100%;
    height: 120px;
    border: 1px solid #487996;
    box-sizing: border-box;
    border-radius: 50px 0px;
    resize: none;
    padding: 12px 16px;
    outline: none;
    margin-top: 4px;
}
@media screen and (min-width: 768px) {
    .modal__form-message {
        width: 420px;
        height: 199px;
    }
}
@media screen and (min-width: 1444px) {
    .modal__form-message {
        width: 515px;
    }
}
.modal__form-info {
    font-weight: normal;
    font-size: 16px;
    line-height: 1.25;
    color: #CDCDCD;

}
.modal__form-btn {
    height: 56px;
    width: 280px;
    font-family: var(--main-font);
    font-style: normal;
    font-weight: bold;
    font-size: 16px;
    line-height: 1.5;
    color: var(--title-color);
    margin-top: 24px;
    margin-bottom: 52px;
  
    text-align: center;
    letter-spacing: 0.1em;
    text-transform: uppercase;
    background: #f5f5fa;
    box-shadow: -10px -10px 20px #ffffff, 10px 10px 20px rgba(72, 121, 150, 0.1),
      5px 5px 10px rgba(72, 121, 150, 0.25), -5px -5px 10px #ffffff;
    border-radius: 30.5px;
  }
  @media screen and (min-width: 768px) {
    .modal__form-btn {
        margin-bottom: 76px;
    }
    .test-btn {
        text-align: right;
    }
    @media screen and (min-width: 1444px) {
        .modal__form-btn {
            margin-bottom: 0px;
        }
        .test-btn {
            width: 515px;
        }
    }
  }

  .contact__info-text {
    text-transform: uppercase;
    margin-bottom: 8px;
  }

  .contact__list:not(:last-child),
  .contact__link:not(:last-child) {
      margin-bottom: 8px;
  }

  .contact__box-tel, 
  .contact__box-email {
      margin-bottom: 24px;
  }
  @media screen and (min-width:1444px) {
    .contact__box-tel, 
    .contact__box-email {
        margin-bottom: 68px;
    }
      
  }
  .contact__box-info {
      display: flex;
      flex-direction: column;
  }

 @media screen and (min-width: 768px) {
     .contact__position-one {
         display: flex;
     }
     .contact__box-info {
         flex-direction: row;
     }
     .contact__box-worktime {
         text-align: right;
     }
     .contact__box-tel {
        width: 247px;
     }
     @media screen and (min-width: 1444px) {
         .contact__position-one {
             margin-bottom: 100px;
         }
         
     }
 }

 @media screen and (min-width: 1444px) {
     .contact__position-four {
        display: flex;
        flex-direction: row-reverse;
        .contact__box-info {
        flex-direction: column;
        margin-right: 275px;
         }
         .contact__box-worktime {
             text-align: left;
         }
         .contact__info-text {
             margin-bottom: 18px;
         }
     }
 }